import React from 'react';
import { useSelector } from 'react-redux';

function UserInfo({ userInfo, setUserInfo }) {
    const { user } = useSelector(state => state.AdminReducer);
    
    return (
        <div className={userInfo ? "user-info active" : "user-info"}>
            <h5>Thng Tin Chi Tit</h5>
            <hr />
            <div className="user-info__content">
                <p>Ti Khon : <span>{user.taiKhoan}</span></p>
                <p>Email : <span>{user.email}</span></p>
                <p>Mt Khu : <span>{user.matKhau}</span></p>
                <p>H Tn : <span>{user.hoTen}</span></p>
                <p>S in Thoi : <span>{user.soDt}</span></p>
                <p>Loi Ngi Dng : <span>{user.maLoaiNguoiDung}</span></p>
            </div>
            <hr />
            <div className="user-info__btn">
                <button className="btn-blue" onClick={() => {
                    setUserInfo(false);
                }}>Quay li</button>
            </div>
        </div>
    );
}

export default UserInfo;